<template>
  <div class="main-padding">
    <el-card>
      <el-form :inline="true">
        <el-form-item>
          <el-button @click="$router.back()">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top: 20px;">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button @click="initIndex">
            初始化ES索引
          </el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="importES">
            将数据从数据库导入ES
          </el-button>
        </el-form-item>
      </el-form>
      <h4>从文件夹加载图片</h4>
      <el-form :inline="true" :model="initForm" class="demo-form-inline">
        <el-form-item>
          <el-input
            v-model="initForm.folderPath"
            placeholder="路径"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-select
            v-model="initForm.userId"
            filterable
            remote
            clearable
            reserve-keyword
            placeholder="临时用户ID"
            :remote-method="remoteMethod"
            :loading="optionsLoading"
          >
            <el-option
              v-for="item in userList"
              :key="item.phone"
              :label="item.name + `(${item.phone})`"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-radio-group v-model="initForm.privacy">
            <el-radio
              :label="key"
              v-for="(item, key) in $enum.PrivacyState"
              :key="key"
              >{{ item }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-button @click="init">
          初始化
        </el-button>
      </el-form>
      <h4>采集失败标识不再采集</h4>
      <el-form :inline="true" :model="giveUpErrorForm" class="demo-form-inline">
        <el-form-item>
          <el-input
            v-model="giveUpErrorForm.status"
            placeholder="状态码"
            clearable
          ></el-input>
        </el-form-item>
        <el-button @click="giveUpError">
          放弃标识
        </el-button>
      </el-form>
      <h4>绑定用户（采集后处理）</h4>
      <el-form :inline="true" :model="initForm" class="demo-form-inline">
        <el-form-item>
          <el-select
            v-model="bindUserForm.userId"
            filterable
            remote
            clearable
            reserve-keyword
            placeholder="临时用户ID"
            :remote-method="remoteMethod"
            :loading="optionsLoading"
          >
            <el-option
              v-for="item in userList"
              :key="item.phone"
              :label="item.name + `(${item.phone})`"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-button @click="bindUser">
          绑定用户
        </el-button>
      </el-form>
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-button @click="checkTag">
            检查空tag的数量
          </el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="duplicateRemoval">
            清理重复tag
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { mapActions } from "vuex"

export default {
  data() {
    return {
      initForm: {
        folderPath: "",
        userId: "",
        privacy: this.$enum.getKey(
          this.$enum.PrivacyState,
          this.$enum.PrivacyState.PUBLIC
        )
      },
      optionsLoading: false,
      userList: [],
      bindUserForm: {
        userId: ""
      },
      giveUpErrorForm: {
        status: null
      }
    }
  },
  methods: {
    ...mapActions("user", { AListUser: "AList" }),
    ...mapActions("picture", [
      "AInit",
      "ABindUser",
      "AInitIndex",
      "AImportES",
      "ACheckTag",
      "ADuplicateRemoval",
      "AGiveUpError"
    ]),
    async init() {
      const result = await this.AInit(this.initForm)
      console.log(result)
    },
    async bindUser() {
      const result = await this.ABindUser(this.bindUserForm)
      console.log(result)
    },
    async remoteMethod(name) {
      if (name !== "") {
        this.optionsLoading = true
        this.userList = (await this.AListUser({ name })).data
        this.optionsLoading = false
      } else {
        this.userList = []
      }
    },
    async initIndex() {
      const result = await this.AInitIndex()
      console.log(result)
    },
    async importES() {
      const result = await this.AImportES()
      console.log(result)
    },
    async checkTag() {
      const result = await this.ACheckTag()
      console.log(result)
    },
    async duplicateRemoval() {
      const result = await this.ADuplicateRemoval()
      console.log(result)
    },
    async giveUpError() {
      const result = await this.AGiveUpError(this.giveUpErrorForm)
      console.log(result)
    }
  }
}
</script>

<style type="text/less" lang="less" scoped>
h4 {
  margin-bottom: 16px;
}
</style>
